<template>
  <el-container class="create">
    <!--  -->
    <el-main class="create-wrap">
      <el-form ref="formCreate" :model="form" :rules="rules" label-width="80px">
        <el-form-item label="" prop="tab">
          <el-select v-model="form.tab" placeholder="">
            <el-option label="" value="share"></el-option>
            <el-option label="" value="ask"></el-option>
            <el-option label="" value="job"></el-option>
            <el-option label="" value="dev"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="title">
          <el-input v-model="form.title" placeholder="10"></el-input>
        </el-form-item>
        <el-form-item label="" prop="content">
          <!-- <el-input type="textarea" v-model="form.content"></el-input> -->
          <markdown-editor
            v-model="form.content"
            :sanitize="false"
            :configs="configs"
            preview-class="markdown-body"
            :autoinit="true">
          </markdown-editor>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('formCreate')"></el-button>
          <el-button @click="resetForm('formCreate')"></el-button>
        </el-form-item>
      </el-form>

    </el-main>
    <!--  -->
    <el-aside class="hidden-sm-and-down"  width="200px">
      <v-aside-user :userInfo="userInfo"></v-aside-user>
      <v-aside-qr></v-aside-qr>
    </el-aside>
  </el-container>
</template>

<script>
import AsideUser from '@/components/AsideUser'
import AsideQR from '@/components/AsideQR'
import markdownEditor from 'vue-simplemde/src/markdown-editor'

export default{
  name:'Create',
  data(){
    return{
      form: {
        title: '',
        tab: '',
        content: '',
      },
      configs: {
        status: false, // 
        spellChecker: false // 
      },
      rules:{
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 10,  message: ' 10 ', trigger: 'blur' }
        ],
        tab: [
          { required: true, message: '', trigger: 'blur' }
        ],
        content: [
          { required: true, message: '', trigger: 'blur' }
        ],
      }
    }
  },
  components:{
    'v-aside-user':AsideUser,
    'v-aside-qr':AsideQR,
    markdownEditor
  },
  computed:{
    userInfo(){
      return this.$store.state.userInfo
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$api.createTopics({
            accesstoken:this.userInfo.accesstoken,
            ...this.form
          })
          .then(res => {return JSON.parse(res)})
          .then(res => {
            this.$router.push(`/topics/${res.topic_id}`);
          })
          .catch(err=>{
            console.log(err)
          })
        } else {
          this.$message('');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
}
</script>

<style lang="scss" scoped>
.create{
  .create-wrap{
    background: #fff;
    .el-form{
      padding: 10px;
    }
  }
}
</style>



